.scroll-center-modal {
  bottom: 20px;
  top: 50px;
  overflow: hidden;
}
.scroll-center-modal .ant-modal {
  height: 100%;
  overflow: hidden;
}
.scroll-center-modal .ant-modal-content {
  height: 100%;
}
.scroll-center-modal .ant-modal-header {
  position: absolute;
  background: #fff;
  top: 0;
  width: 100%;
  height: 55px;
  z-index: 2;
}
.scroll-center-modal .ant-modal-body {
  overflow: auto;
  height: 100%;
  padding-top: 65px;
  padding-bottom: 68px;
  box-sizing: border-box;
}
.scroll-center-modal .ant-modal-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 53px;
  background-color: #fff;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  box-shadow: 0 0 0 60px #fff inset;
  -webkit-text-fill-color: #878787;
}
@media (-webkit-max-device-pixel-ratio: 1) {
  ::-webkit-scrollbar-track-piece {
    background-color: #ccc;
  }
  ::-webkit-scrollbar {
    width: 7px;
    height: 7px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #767777;
    background-clip: padding-box;
    min-height: 28px;
    border-radius: 3px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background-color: #8b8e8e;
  }
}
