.layoutComponent {
  width: 100%;
}
.layoutComponent .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}
.layoutComponent__header {
  width: 100%;
  background: #fff;
  padding: 0;
}
.layoutComponent__header .col-user {
  margin-right: 20px;
  float: right;
}
.layoutComponent .ant-breadcrumb {
  padding: 14px 16px 0;
}
.layoutComponent__content {
  padding: 14px 16px 0;
  width: 100%;
}
.layoutComponent__content .ant-breadcrumb {
  margin-bottom: 10px;
}
.layoutComponent__content > div {
  padding: 20px;
  width: 100%;
  min-height: 600px;
  background: #fff;
  box-sizing: border-box;
}
.layoutComponent .ant-menu-item a {
  color: #fff;
  text-decoration: none;
}
.ant-drawer-body {
  padding: 0;
}
.ant-drawer-content {
  background-color: #000818;
}
.logo {
  margin: 16px 20px 30px;
  height: 34px;
  color: #fff;
  overflow: hidden;
}
.logo img {
  width: 34px;
}
.logo span {
  display: inline-block;
  margin-left: 10px;
  font-size: 16px;
}

